<template>
<div class="menuBtnDown menu-button" :class="{'bg-dark': darkBg}" style="z-index: 9999">
  <button
    type="button"
    class="btn border-0 rounded-0 p-3 p-sm-5"
    ref="menu"
    @click="toggleModal"
  >
    <div class="bar"></div>
    <div class="bar"></div>
    <div class="bar"></div>
  </button>
</div>
  <div
    class="offcanvas offcanvas-end hambugSet"
    tabindex="-1"
    data-bs-backdrop="static"
    data-bs-scroll="true"
    aria-labelledby="offcanvasExampleLabel"
    ref="modal"
  >
    <div class="offcanvas-body d-flex flex-column justify-content-between py-6">
      123
    </div>
  </div>
</template>

<style scoped>
.menuBtnDown {
  top: 0;
  right: 0;
}
.hambugSet {
  min-width: 70%;
}
.menuBtnDown:hover {
  background-color: #000;
}

@media (max-width: 576px) {
  .hambugSet {
    min-width: 100%;
  }
  .menuBtnDown {
    background-color: #000;
  }
  .cross .bar:nth-of-type(1) {
    background-color: #fff;
  }
  .cross .bar:nth-of-type(3) {
    background-color: #fff;
  }
  .btnBgSet {
    background-color: #fff;
  }
}

.menu-button {
  position: fixed;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.menu-button .bar:nth-of-type(1) {
  margin-top: 0px;
}
.menu-button .bar:nth-of-type(3) {
  margin-bottom: 0px;
}
.bar {
  position: relative;
  display: block;
  width: 30px;
  height: 2px;
  margin: 8px auto;
  background-color: #fff;
  border-radius: 10px;
  -webkit-transition: 0.3s;
  transition: 0.3s;
}
.menu-button:hover .bar:nth-of-type(1) {
  -webkit-transform: translateY(1.5px) rotate(-4.5deg);
  -ms-transform: translateY(1.5px) rotate(-4.5deg);
  transform: translateY(1.5px) rotate(-4.5deg);
  background-color: #fff;
}
.menu-button:hover .bar:nth-of-type(2) {
  opacity: 0.9;
}
.menu-button:hover .bar:nth-of-type(3) {
  -webkit-transform: translateY(-1.5px) rotate(4.5deg);
  -ms-transform: translateY(-1.5px) rotate(4.5deg);
  transform: translateY(-1.5px) rotate(4.5deg);
  background-color: #fff;
}
.cross .bar:nth-of-type(1) {
  -webkit-transform: translateY(10px) rotate(-45deg);
  -ms-transform: translateY(10px) rotate(-45deg);
  transform: translateY(10px) rotate(-45deg);
  background-color: #000;
}
.cross .bar:nth-of-type(2) {
  opacity: 0;
}
.cross .bar:nth-of-type(3) {
  -webkit-transform: translateY(-10px) rotate(45deg);
  -ms-transform: translateY(-10px) rotate(45deg);
  transform: translateY(-10px) rotate(45deg);
  background-color: #000;
}
.cross:hover .bar:nth-of-type(1) {
  -webkit-transform: translateY(10px) rotate(-35deg);
  -ms-transform: translateY(10px) rotate(-35deg);
  transform: translateY(10px) rotate(-35deg);
}
.cross:hover .bar:nth-of-type(2) {
  opacity: 0;
}
.cross:hover .bar:nth-of-type(3) {
  -webkit-transform: translateY(-10px) rotate(35deg);
  -ms-transform: translateY(-10px) rotate(35deg);
  transform: translateY(-10px) rotate(35deg);
}
</style>

<script>
import { Offcanvas } from 'bootstrap';

export default {
  data() {
    return {
      darkBg: false,
    };
  },
  methods: {
    toggleModal() {
      const addCross = this.$refs.menu;
      //   判斷是否有cross類別
      addCross.classList.toggle('cross');
      addCross.classList.toggle('btnBgSet');
      this.offcanvas.toggle();
    },
  },
  mounted() {
    this.offcanvas = new Offcanvas(this.$refs.modal);
    window.addEventListener('scroll', () => {
      const windowY = window.scrollY;
      const windowX = window.innerWidth;
      const breakpoint = document.getElementById('breakpoint');
      console.log(windowX);
      if (windowY > breakpoint.offsetTop && windowX > 576) {
        this.darkBg = true;
      } else {
        this.darkBg = false;
      }
    });
  },
};
</script>
